import React from 'react';
import { FaLinkedin, FaGithub, FaTwitter, FaFileAlt } from 'react-icons/fa';
import { IoMdMail } from 'react-icons/io';
import dilpImage from '../assets/dilp.jpg';
import sreeImage from '../assets/sree.jpg';
import naruImage from '../assets/naru.jpg';
import manojImage from '../assets/manoj.jpg';

import naruResume from '../assets/resume.pdf';
import manojResume from '../assets/resume-manoj.pdf';

import { Link } from 'react-router-dom';
import './TeamPage.css';

const TeamPage = () => {
  const teamMembers = [
    {
      name: "Narayana Reddy Naru",
      title: "Full Stack Developer",
      image: naruImage,
      linkedin: "https://www.linkedin.com/in/narayana1726/",
      github: "https://github.com/narayana2607",
      twitter: "https://x.com/narayana1726",
      email: "mailto:narayana01704@gmail.com",
      bio: "Specializing in React, Node.js, and cloud architectures with 5+ years of experience building scalable web applications.",
      resume: naruResume,
      skills: ["React", "java","Node.js", "AWS", "JavaScript"]
    },
    {
      name: "Sreedhar Kanuboina",
      title: "Senior Data Engineer", 
      image: sreeImage,
      linkedin: "https://www.linkedin.com/in/sreedhar-kanuboina-24602a196/",
      github: "#",
      twitter: "#",
      email: "mailto:sreedhar@example.com",
      bio: "Expert in big data pipelines, ETL processes, and cloud data solutions with extensive experience in Spark and Hadoop ecosystems.",
      skills: ["Spark", "Hadoop", "Python", "Azure"]
    },
    {
      name: "Dileep Venkata Chowdary",
      title: "Senior Network Engineer",
      image: dilpImage,
      linkedin: "https://www.linkedin.com/in/dileep-e-348b4a2b8/",
      github: "#",
      twitter: "#",
      email: "mailto:dileep@example.com",
      bio: "Network infrastructure specialist with Cisco certifications and expertise in cloud networking and security solutions.",
      skills: ["Cisco", "AWS", "Firewalls", "SD-WAN"]
    },
    {
      name: "Manoj Kumar Penugonda",
      title: "Full Stack Developer",
      image: manojImage,
      linkedin: "https://www.linkedin.com/in/manoj-kumar-penugonda-7b59361aa/",
      github: "#",
      twitter: "#",
      email: "mailto:penugondamanojkumar2020@gmail.com",
      bio: "Skilled in modern JavaScript frameworks with a focus on performance optimization and clean architecture.",
      resume: manojResume,
      skills: ["Java", "C++", "MongoDB", "GraphQL"]
    }
  ];

  return (
    <div className="team-page">
      <div className="team-container">
        <header className="team-header">
          <div className="header-content">
            <h1>Our <span>Expert</span> Team</h1>
            <p className="subtitle">Meet the professionals driving innovation and excellence</p>
            <div className="header-divider"></div>
          </div>
        </header>

        <div className="team-grid">
          {teamMembers.map((member, index) => (
            <div key={index} className="team-card">
              <div className="card-image-container">
                <img 
                  className="card-image" 
                  src={member.image} 
                  alt={member.name}
                  loading="lazy"
                />
                <div className="image-overlay"></div>
              </div>
              
              <div className="card-content">
                <div className="member-info">
                  <h3>{member.name}</h3>
                  <p className="title">{member.title}</p>
                </div>
                
                <p className="member-bio">{member.bio}</p>
                
                <div className="skills-container">
                  {member.skills.map((skill, i) => (
                    <span key={i} className="skill-tag">{skill}</span>
                  ))}
                </div>

                <div className="card-footer">
                  <div className="social-links">
                    <a href={member.linkedin} target="_blank" rel="noopener noreferrer" aria-label="LinkedIn">
                      <FaLinkedin className="icon linkedin" />
                    </a>
                    <a href={member.github} target="_blank" rel="noopener noreferrer" aria-label="GitHub">
                      <FaGithub className="icon github" />
                    </a>
                    <a href={member.twitter} target="_blank" rel="noopener noreferrer" aria-label="Twitter">
                      <FaTwitter className="icon twitter" />
                    </a>
                    <a href={member.email} aria-label="Email">
                      <IoMdMail className="icon email" />
                    </a>
                  </div>

                  {member.resume && (
                    <a 
                      href={member.resume} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      className="resume-button"
                    >
                      <FaFileAlt className="resume-icon" /> View CV
                    </a>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>

        <div className="join-team">
          <div className="join-content">
            <h2>Ready to Join Our Team?</h2>
            <p>We're always looking for talented professionals to join our growing team of experts.</p>
            <Link to="/contact" className="contact-button">
              Contact Us
              <span className="button-arrow">→</span>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TeamPage;